/* global*/

:root {
  --navHt    : 65px;
  --widthPage: 1100px;
  --purple   : #303F9F; 
  --red      : #be3434;
  --indigo   : #3a5ad5; 
  --lgPurple : #C5CAE9;
  --accent   : #7C4DFF; 
  --text     : #212121;
  --mGray    : #757575; 
  --sGray    : #BDBDBD; 
  /* new colors material ui */
  --primary10: #002117;
  --primary20: #00382A;
  --primary30: #00513E;
  --primary40: #006B54;
  --primary50: #00876A;
  --primary60: #17A382;
  --primary70: #42BF9B;
  --primary80: #62DBB6;
  --primary90: #80F8D1;
  --primary95: #BAFFE5;
}
@font-face {
  font-family: "Emberly";
  src: local("Emberly"),
  url("./fonts/Emberly/Emberly-Bold.ttf") format("truetype");
  font-weight: bold;
}
@font-face {
  font-family: "Wild-World";
  src: local("Wild-World"),
  url("./fonts/wild-world.otf") format("opentype");
  font-weight: bold;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto"),
  url("./fonts/Roboto/Roboto-Medium.ttf") format("truetype");
  font-weight: 300;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

/* utils */

.flexcenter{
  display: flex;
  align-items:center;
  justify-content:center;
}

.home_section {
  width: 100vw; 
  height: calc(100vh - var(--navHt));
  display: flex;
  align-items: center;
  justify-content: center;
}

.page{
  margin-top: var(--navHt);
  overflow: hidden;
  position: relative;
  background-color: #f000;
  right: 0px;
  animation-duration: 0.6s;
}

.shadow{
  -webkit-box-shadow: 0px 4px 15px 1px #00000048;
  box-shadow: 0px 4px 15px 1px #00000048;
}


.column-container{
  display:grid;
  grid-template-columns: 50% 50%;
}

.column-container div{
  margin: 0 auto; 
}
.oval-module{
  width: var(--widthPage);
  max-width: 80%;
  padding: 50px 0;
  margin-top: 70px;
  margin-bottom: 90px;
  border-radius: 80px;
}

/* intro */

.intro-text{
  margin: 0 auto;
  display: flex;
  justify-content:center;
  flex-direction: column;
  width: 300px;
  max-width:80%;
  margin-bottom:800px;
}

.intro-text li{
  margin: 5px;
}

.intro-text h2{
  font-size: 40px;
  color: var(--indigo);
  margin-bottom: 5px;
}

.logo-b{
  margin-top: 40px;
  height: 450px;
  width: 370px;
}


/* content  */
.paragraph{
  margin: 0 auto; 
  width: var(--widthPage);
  max-width: 80%;
  margin-top: 80px;
}

.paragraph p{
  margin: 0 auto;
  width: 350px;
  margin-top: 50px;
}

.paragraph img{
  width: 400px;
}

/* previous */

.portfolio{
  margin: 0 auto;
  margin-top: 70px;
  margin-bottom: 120px;
  height: calc(100vh - var(--navHt));
  width: var(--widthPage);
  max-width: 80%;
  justify-content: space-around;
  flex-direction: column;
}
/* nav */

.navbar{
  position: fixed;
  top: 0px;
  height: calc(var(--navHt) + 10px);
  width: 100%;
  background-color: #111;
  color: white;
  justify-content: space-between;
  display: flex;
  align-items: center;
  z-index: 19;
}

.exit-back {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #0000;
  z-index: -1;
}
.item-mobile-menu {
  background-color: #fff0;
  width: 90%;
  padding: 10px 0px;
  border-radius: 40px;
  color: #fff;
  border: 0px;
  font-size: 20px;
}

.item-mobile-menu:hover {
  color: var(--primary95);
  background-color: var(--primary20);
  transition: 0.5s;
}

.mobile-menu-back {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #00000080;
  backdrop-filter: blur(5px);
  top: 0;
  left: 0;
  z-index: 3;
}

.mobile-menu {
  position: fixed;
  width: 95%;
  height: 300px;
  background-color: #222;
  bottom: 0;
  left: 2.5%;
  display: flex;
  border-radius: 40px 40px 0px 0px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
 
.nav-img {
  height: 45px;
  width: auto;
  margin-left: 30px;
}

.sm {
  height: 25px;
}
.left-navbar {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  gap: 10px;
}

.nav-menu {
  height: 30px;
  width: auto;
  margin-right: 30px;
}

.nv-top {
  background-color: #fff;
}

.item-container{
  display: flex;
  align-items:center;
  flex-direction: row-reverse;
  margin-right: 10px;
  transition: 0s;
}

.modal-languages-back {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  backdrop-filter: blur(5px);
  background-color: #00000080;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
}
.modal-languages {
  height: 300px;
  width: 300px;
  background-color: #111;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.modal-languages-button {
  height: 40px;
  width: 90%;
  color: #fff;
  border-radius: 10px;
  background-color: #fff0;
  border: 0px;
  font-size: 15px;
  font-family: 'Roboto';
}

.exit-alt {
  color: #d11;
}

.modal-languages-button:hover {
  color: var(--primary90);
  background-color: var(--primary20);
  transition: 0.5s;
}

.exit-alt:hover {
  color: #f55;
  background-color: #300;
  transition: 0.5s;
}

.item-nav {
  height: 40px;
  margin: 0 18px 0 0;
  padding: 0 10px 0 10px;
  display:flex;
  align-items: center;
  border: none;
  font-size: 16px;
  justify-content: center;
  color: #222;
  background-color: #0000;
  font-family: 'Roboto';
  border-color: var(--indigo);
  border-radius: 10px;
}

.item-nav:hover {
  color: var(--primary10);
  background-color: var(--primary95);
  transition: 0.5s;
}

.item-w {
  color: #fff;
  border-color: #fff;
}

.item-w:hover {
  color: var(--primary95);
  background-color: var(--primary40);
  transition: 0.5s;
}
/* intro carousel */

.image-slider {
  height: 100vh;
  width: 100%;
  display: flex;
  overflow: hidden;
}

.image-container {
  transition: 0.6s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
}
.img-full {
  height: 100vh;
  width: 100vw;
  object-fit: cover;
  filter: brightness(30%) blur(2px);
}

.image-slider_dots_cont {
  position: absolute;
  bottom: 20px;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.image-slider_dot {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 2px solid #fff;
  background-color: #fff0;
}

.active_dot {
  background-color: #fff;
  border: 2px solid #000;
  outline: 2px solid #fff;
}

.right-slide {
  position: absolute;
  right: 2px;
  height: 100%;
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0000;
  border: none;
}

.image-slider_text {
  position: absolute;
  top: 60%;
  width: 420px;
  max-width: 70%;
  align-self: center;
  color: #fff;
  text-align: justify;
  font-size: 18px;
  font-family: 'Roboto';
}
.image-slider_text h1 {
  text-align: center;
  font-size: 30px;
  margin-bottom: 20px;
}

.arrow-img {
  filter: invert(100%);
}

.inverted-img {
  transform: scaleX(-1);
}

.left-img {
  right: none;
  left: 2px;
}

/* parallax */

.parallax-section {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  overflow: hidden;
}

.frontend-container {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.container-fluid {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.final-paralax-container {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 300vh;
  background-color: #fff;
  transition: 0.5s ease-in-out;
}

.black-square-cont {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #111;
}

.text-frontend {
  font-family: 'Roboto';
  font-size: 40px;
  text-align: justify;
  text-justify: inter-word;
  width: 600px;
  background-color: #f000;
  color: #222;
}

.first-sec div {
  z-index: 0;
}

.img-flutter {
  width: 600px;
}

.darkstyle {
  width: 600px;
  color: #fff;
}

.em-first {
  color: var(--primary90);
  font-style: normal;
}
.em-second {
  color: var(--primary95);
  font-style: normal;
}
.em-third {
  color: var(--primary80);
  font-style: normal;
}
.text-frontend-em {
  font-style: normal;
  color: var(--primary60);
}

.section-starting {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: #f000;
}

#black-circle {
  border-radius: 50%;
  background-color: #111;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
}

/* certification section  */
#certification-container {
  z-index: 8;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: #fff;
}

.cert-bg {
  position: absolute;
  z-index: 1;
  width: 40px;
}

#cert-bg1 {
  left: 10%;
}

#cert-bg2 {
  right: 10%;
}

.cert-bg h3 {
  font-family: 'Roboto';
  font-size: 50px;
  color: #ccc;
}

.certs-text {
  position: absolute;
  width: 1000px;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto';
}

.certs-text h1 {
  font-family: 'Roboto';
  font-size: 30px;
  color: #222;
  margin-bottom: 40px;
}

.cert-card {
  text-decoration: none;
  z-index: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 150px;
  padding: 0 20px;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.3);
  margin-bottom: 30px;
  background-color: #fff;
  transition: 0.3s ease-in-out;
}

.cert-card:hover {
  box-shadow: 0px 0px 8px 2px rgba(0,0,0,0.5);
}

.cert-title {
  font-family: 'Roboto';
  font-size: 20px;
  margin: 0px;
  margin-bottom: 10px;
  padding: 0px 20px;
  padding-top: 40px;
  color: #222;
}

.cert-text {
  font-family: 'Roboto';
  font-size: 18px;
  padding: 0px 20px;
  margin: 0px;
  color: #444;
  padding-bottom: 40px;
}

/* projects section */
#projects-container {
  position: relative;
  z-index: 10;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  overflow: hidden;
  background-color: #111;
}

.project-content {
  width: 80%;
  display: flex;
  flex-direction: column;
  margin-top: 100px;
}

.project-content h1 {
  font-family: 'Roboto';
  font-size: 50px;
  color: #fff;
  padding-bottom: 80px;
}

.projects {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}

.project-card {
  text-decoration: none;
  width: 680px;
  height: 500px;
  border-radius: 20px;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  margin-bottom: 150px;
  margin-right: 40px;
  transition: 0.3s ease-in-out;
  color: #fff;
}

.project-card:hover {
  color: var(--primary90);
}


.project-card-img {
  width: 100%;
  height: 400px;
  border-radius: 20px;
  margin-bottom: 20px;
  overflow: hidden;
}

.project-card-img img {
  width: 100%;
  height: 100%;
  transition: transform .4s;
  transform: scale(1.2);
}

.project-card:hover img {
  transform: scale(1);
}

.project-card h2 {
  font-family: 'Roboto';
  font-size: 25px;
  margin: 0px;
  margin-bottom: 10px;
}

/* projects pages */

/* teral */

.teral-article {
  width: 60%;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 80px 0;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.3);
  background-color: #fff;
}
.teral-article-content {
  padding: 0 40px;
}

.teral-article h1 {
  font-family: 'Roboto';
  font-size: 50px;
  color: var(--primary70);
  padding-bottom: 30px;
}

.teral-article p {
  font-family: 'Roboto';
  font-size: 20px;
  color: #111;
  padding-bottom: 20px;
}

.teral-article a {
  font-family: 'Roboto';
  font-size: 20px;
  color: var(--primary80);
  padding-bottom: 20px;
}

.teral-img-1 {
  width: 200px;
  border-radius: 20px;
  margin-bottom: 20px;
  overflow: hidden;
}

.teral-img-2 {
  width: 400px;
  margin-bottom: 20px;
  overflow: hidden;
}
